<template>
  <div class="my-conent">
    <div class="user-info">
      <div class='my-onlogin' v-if='token'>
        <div class="user-base">
          <div class="left-user-base">
            <van-image
              round
              width="65px"
              height="65px"
              class="my-pho"
              :src="onUser.photo"
            />
            <span>{{onUser.name}}</span>
          </div>
          <button class="right-user-base" @click="$router.push('/setMy')">编辑资料</button>
        </div>
        <ul class="user-data">
          <li>
            <p>{{onUser.art_count || 0}}</p>
            <p>头条</p>
          </li>
          <li>
            <p>{{onUser.follow_count || 0}}</p>
            <p>关注</p>
          </li>
          <li>
            <p>{{onUser.fans_count || 0}}</p>
            <p>粉丝</p>
          </li>
          <li>
            <p>{{onUser.like_count || 0}}</p>
            <p>获赞</p>
          </li>
        </ul>
      </div>
      <div class='my-unlogin' @click="$router.push('/login?r=/my')" v-else>
        <img src="../../assets/hea.png" alt="" style="width: 66px;height: 66px;">
        <span>登录/注册</span>
      </div>
    </div>
    <van-grid :column-num="2" class='my-nav'>
      <van-grid-item icon-prefix="icon" icon="shoucang" text="收藏" />
      <van-grid-item icon-prefix="icon" icon="lishi" text="历史" />
    </van-grid>
    <div class="back"></div>
    <div>
      <van-cell title="消息通知" is-link to="/" v-if="token"/>
      <van-cell title="小智同学" is-link to="/my/userCart" />
    </div>
    <div class="back"></div>
    <van-cell title="退出登录" class="my-button" @click='outTo' v-if='token' />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { userData } from '@/api/user.js'

export default {
  name: 'MyIndex',
  components: {},
  props: {},
  data () {
    return {
      // 存储当前登录用户信息
      onUser: {}
    }
  },
  computed: {
    // 映射vuex中的token数据，方便使用
    ...mapState([
      'token'
    ])
  },
  watch: {},
  created () {
    this.uData() // 生命周期函数调用方法
  },
  mounted () {},
  methods: {
    // 获取用户身份信息
    async uData () {
      try {
        const { data } = await userData()
        this.onUser = data.data
      } catch (err) {
        console.log(err)
      }
    },
    outTo (confirmButtonColor) {
      // vant组件中的确认弹框
      this.$dialog.confirm({
        title: '确认退出',
        message: '您确定要退出吗？',
        confirmButtonColor: '#3d9bf8'
      })
        // 确认执行then
        .then(() => {
          this.$store.commit('delToken') // 确认退出登录就直接提交到vuex中去删除本地储存的用户数据
        })
        // 取消执行catch
        .catch(() => {
          // on cancel
        })
    }
  }
}
</script>

<style scoped>
  .user-info{
    width: 100%;
    height: 201px;
    background: url("../../assets/mybackground@3x.png") no-repeat;
    background-size: 100% 201px;
    position: relative;
  }
  .user-base{
    padding-top: 50px;
    margin: 0 16px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .left-user-base{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .left-user-base>span{
    padding-left: 10px;
    color: #fff;
    font-size: 15px;
  }
  .my-unlogin{
    margin: 0 auto;
    padding-top: 54px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .my-unlogin>span{
    color: #fff;
    font-size: 14px;
    margin-top: 8px
  }
  .my-pho{
    border: 2px solid #fff !important;
    box-sizing: border-box;
  }
  .right-user-base{
    padding: 0 10px;
    height: 25px;
    background-color: #fff;
    color: #666;
    border: none;
    font-size: 10px;
    border-radius: 13px;
  }
  .user-data{
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: 15px
  }
  .user-data>li{
    flex-direction: column;
    display: flex;
    align-items: center;
  }
  .user-data *{
    color: #fff;
    box-sizing: border-box;
  }
  .user-data>li>p:nth-child(1){
    font-size: 18px;
  }
  .user-data>li>p:nth-child(2){
    font-size: 12px;
    margin-top: 6px;
  }
  .my-nav{
    height: 70px;
  }
  .my-nav>.van-grid-item{
    height: 70px;
  }
  .my-nav>.van-grid-item>.van-grid-item__text{
    font-size: 14px;
    color: #333
  }
  /deep/ .icon-shoucang{
    font-size: 22.5px;
    color: #eb5253;
  }
  /deep/ .icon-lishi{
    font-size: 22.5px;
    color: #ff9d1d
  }
  .back{
    height: 5px;
    background-color: rgba(0, 0, 0, 0);
  }
  .my-button{
    color: #D86262;
    background-color: #fff;
    border: unset;
    text-align: center;
  }
</style>
